.listItem{
	/*opacity: 0.4;*/
	background-color: red;
	border: 1px;
	margin: 5px;
	padding: 5px;
	width: 100px;
	height: 100px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	-ms-flex-align: center;
	    align-items: center;
}

.table-cell
{
	border-top: 1px solid #999;
	height: 50px;
}

.forecast-summary{
	/*display: inline-block;*/
	padding-left: 5px;
	text-align: left;
}

.forecast-day{
	/*width: 150px;	*/
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	padding: 5px 5px;
}

.cell1
{
	border-right: 3px solid #999;
}

.today{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	font-family: Helvetica;
	font-size: .85rem;
}

#current-icon-daily{
	height: 40px;
	width: 40px;
}


